$positive: white;
$negative: black;
$theme-1-primary: rgb(93, 202, 202);
$theme-2-primary: rgb(207, 72, 207);
$theme-3-primary: rgb(192, 192, 59);
$theme-4-primary: gainsboro;
$line-color: rgb(197, 77, 77);

// light mode
$ipositive: black;
$inegative: white;
$itheme-1-primary: rgb(162, 53, 53);
$itheme-2-primary: rgb(48, 183, 48);
$itheme-3-primary: rgb(63, 63, 196);
$itheme-4-primary: rgb(169, 169, 169);
$iline-color: rgb(58, 178, 178);

.mainme {
    position: absolute;
    width: 100%;
    height: 100%;
    font-family: "MajorMono";
}

.logo {
    background-image: url(../Logo.svg);
    background-size: 400px 400px;
    opacity: 60%;
    width: 400px;
    height: 400px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: -8;
    top: 0;
}

.logo path {
    transition: stroke 0.2s ease-in-out;
}

.logo path.logobar1 {
    stroke: var(--theme-1-primary);
}

.logo path.logobar2 {
    stroke: var(--theme-2-primary);
}

.logo path.logobar3 {
    stroke: var(--theme-3-primary);
}

.logo path.logobar4 {
    stroke: var(--theme-4-primary);
}

#mecont {
    font-size: 200px;
    //opacity: 60%;
    width: 100%;
    text-align: center;
    position: relative;
    top: 0px;
    pointer-events: none;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    //padding-bottom: 200px;
    margin-top: 250px;
}

#mecont iframe {
    width: 80%;
    pointer-events: all;
    margin-left: auto;
    margin-right: auto;
}

#mecont h1 {
    font-size: 100px;
    width: 100%;
    font-weight: normal;
}

#mecont h2 {
    font-size: 50px;
    width: auto;
    //text-align: left;
    padding-left: 50px;
    font-weight: normal;
}